<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <div>
    <Headercomponer title="商品管理">
      <!-- 标题及操作区 -->
      <div slot="extra">

      </div>

      <!-- 主要内容 -->
      <div>
        <el-table :data="goodsList" style="width: 100%" v-loading="loading">
          <el-table-column
            prop="goods_id"
            label="商品ID"
            width="200">
          </el-table-column>

          <el-table-column
            prop=""
            label="商品图"
            width="180px">
            <template #default="{row}">
              <div class="img-box"><img :src="row.goods.goods_image" alt=""></div>
            </template>
          </el-table-column>

          <el-table-column
            prop="goods.goods_name"
            label="商品名称"
            width="auto">
          </el-table-column>

          <el-table-column
            prop="goods_num"
            label="商品数量"
            width="180">
          </el-table-column>

          <el-table-column
            prop=""
            label="商品价格"
            width="auto">
            <template #default="{row}">
              <p class="price-min">￥{{ row.goods.goods_price_min }}</p><span class="price-max">￥{{ row.goods.goods_price_max }}</span>
            </template>
          </el-table-column>

          <el-table-column
            prop="create_time"
            label="创建时间"
            width="auto">
          </el-table-column>

          <el-table-column
          fixed="right"
          label="操作"
          width="110">
          <template slot-scope="scope">
            <el-button @click="handleEdit(scope.row)" type="primary" icon="el-icon-edit" circle></el-button>
            <el-button @click="handleDelt(scope.row)" type="danger" icon="el-icon-delete" circle></el-button>
          </template>
        </el-table-column>
        </el-table>
      </div>

      <!-- 弹窗 -->
      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%"
        >
        <el-form :model="formModel" label-width="80px" :inline="true">
          <el-form-item label="商品ID">
            <el-input v-model="formModel.goods_id" disabled></el-input>
          </el-form-item>
          <el-form-item label="商品图片">
            <div class="img-box"><img :src="formModel.goods_img" alt=""></div>
          </el-form-item>
          <el-form-item label="商品名称">
            <el-input v-model="formModel.goods_name" disabled></el-input>
          </el-form-item>
          <el-form-item label="商品数量">
            <div class="goods-num">
              <el-button @click="onSub" type="primary">-</el-button>
              <el-input @input="changeNum" type="number" v-model="formModel.goods_num" class="input"></el-input>
              <el-button @click="formModel.goods_num += 1" type="primary">+</el-button>
            </div>
          </el-form-item>
          <el-form-item label="商品价格">
            <el-input v-model="formModel.goods_price" disabled></el-input>
          </el-form-item>
          <el-form-item label="创建时间">
            <el-input v-model="formModel.goods_time" disabled></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="onSubmit">确 定</el-button>
        </span>
      </el-dialog>
    </Headercomponer>
  </div>
</template>

<script>
import { getGoodsList, updatedGoodsNum, deleteGoods } from '@/api/goods'
import Headercomponer from '@/components/headercomponer.vue'
import { Message } from 'element-ui'
export default {
  data () {
    return {
      loading: false,
      dialogVisible: false,
      goodsList: [],
      formModel: {
        goods_id: '',
        goods_img: '',
        goods_name: '',
        goods_num: 1,
        goods_price: '',
        goods_time: ''
      }
    }
  },
  methods: {
    // 获取商品列表
    async getDoodsList () {
      this.loading = true
      const { data: { list } } = await getGoodsList()
      this.goodsList = list.reverse()
      this.loading = false
    },

    // 编辑
    handleEdit (row) {
      // console.log(row)
      this.formModel = {
        goods_id: row.goods_id,
        goods_img: row.goods.goods_image,
        goods_name: row.goods.goods_name,
        goods_num: row.goods_num,
        goods_price: row.goods.goods_price_min,
        goods_time: row.create_time,
        goods_sku_id: row.goods_sku_id
      }
      this.dialogVisible = true
    },
    // 提交
    async onSubmit () {
      if (this.formModel.goods_num === '') return Message.error('商品数量不可为空')
      // 调修改商品数量接口
      const { goods_id: goodsId, goods_num: goodsNum, goods_sku_id: goodsSkuId } = this.formModel
      const res = await updatedGoodsNum(goodsId, goodsNum, goodsSkuId)
      Message.success('编辑成功')
      // 重新渲染
      this.getDoodsList()
      this.dialogVisible = false
    },
    // 删除
    handleDelt (row) {
      this.$confirm('此操作将永久删除该商品, 是否继续?', '温馨提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        const arr = []
        arr.push(row.id)
        const res = await deleteGoods(arr)
        console.log(res)
        this.getDoodsList()
        Message({
          type: 'success',
          message: '删除成功!'
        })
      }).catch(() => {
        Message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },

    // 减
    onSub () {
      if (this.formModel.goods_num <= 1) return
      this.formModel.goods_num -= 1
    },

    // 输入修改数量
    changeNum (val) {
      this.formModel.goods_num = val
    }
  },
  created () {
    this.$store.commit('tag/selecMenu', {
      path: this.$route.path,
      label: '商品管理'
    })

    // 调用获取商品列表
    this.getDoodsList()
  },
  components: {
    Headercomponer
  }
}
</script>

<style lang="less" scoped>
.img-box{
  width: 100px;
  height: 100px;
  img{
    width: 100px;
    height: 100px;
  }
}
.price-min{
  color: red;
}
.price-max{
  text-decoration: line-through;
}
.goods-num{
  display: flex;
  .input{
    margin: 0 8px;
  }
}
</style>
